html, body, #root, .App {
  height: 100%;
  margin: 0;
  font-family: Arial, sans-serif;
  overflow: hidden;
}

.App-body {
  margin-top: 15vh;
  height: 85vh;
  overflow-y: auto;
  position: relative;
}

.section {
  position: relative;
  height: 85vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

.wave {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 10%;
  overflow: hidden; 
  line-height: 0;
  background-color: rgb(255, 255, 255);
  color: rgb(255, 255, 255);
}

.wave svg {
  display: block;
  width: 100%;
  height: 100%;

  width: calc(100% + 1.3px);
 
}

.wave-part {
  fill: rgba(255, 105, 180, 0.3); /* Color rosa con 30% de opacidad */
  transition: fill 0.3s ease;
}

.wave-part:hover {
  fill: rgba(255, 105, 180, 0.5); /* Cambio de color al pasar el ratón por encima */
}

.content {
  z-index: 1; /* Asegura que el contenido esté sobre la onda */
}
