.PoliticaCookies-body p {
  margin: 0;
  color: aliceblue;
  font-family: Roboto, sans-serif;
  font-size: 14px; /* Ajusta el tamaño del texto según sea necesario */
}

.accion_home {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  box-sizing: border-box;
}

.accion_home a {
  color: #ff8400;
  text-decoration: none;
}
.accion_home a:hover {
  text-decoration: underline;
}

.PoliticaCookiesBody {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  margin: 20px;
  padding: 20px;
  background-color: #f9f9f9;
}
.PoliticaCookiesBody h1, h2, h3 {
  color: #333;
}
.PoliticaCookiesBody p {
  margin: 15px 0;
}
.PoliticaCookiesBody a {
  color: #ff8400;
  text-decoration: none;
}
.PoliticaCookiesBody a:hover {
  text-decoration: underline;
}

