.NosotrosBody {
  display: flex;
  justify-content: center; /* Alinea horizontalmente */
  align-items: center;     /* Alinea verticalmente */
  height: 60vh;           /* Ocupa la altura completa de la ventana */
  padding: 20px;           /* Opcional: agrega espacio interno alrededor */
  color: #826363;
  background-color: #fff9f9;
  background-image: url('./img/fondo_nosotros.jpg');
  background-size: cover; /* Hace que la imagen cubra todo el fondo */
  background-position: center; /* Centra la imagen */
  background-repeat: no-repeat; /* Evita que la imagen se repita */
}

.NosotrosMensaje {
  max-width: 500px;        /* Ancho máximo para limitar el tamaño del contenido */
  text-align: center;      /* Centra el texto */
  color: #826363; 
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.NosotrosMensajeTitulo {
  color: #826363;
  font-family: Roboto, sans-serif;
  font-size: 42px;
}

/* Media Query para pantallas pequeñas (móviles) */
@media (max-width: 768px) {
  .NosotrosBody {
    display: flex;
    justify-content: center; /* Alinea horizontalmente */
    align-items: center;     /* Alinea verticalmente */
    height: 60vh;           /* Ocupa la altura completa de la ventana */
    padding: 20px;           /* Opcional: agrega espacio interno alrededor */
    color: #826363;
    background-color: #fff9f9;
    background-image: url('./img/fondo_nosotros_movil.jpg');
    background-size: cover; /* Hace que la imagen cubra todo el fondo */
    background-position: center; /* Centra la imagen */
    background-repeat: no-repeat; /* Evita que la imagen se repita */
  }

}

