.Productos {
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #eed3d3;
}

.product-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  width: 95%;
  max-width: 1200px;
}

.product-card {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 20px;
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fdebeb
}

.product-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-image {
  width: 100%;
  height: auto;
  max-height: 300px;
  object-fit: cover;
  border-radius: 10px;
}

.product-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.product-name {
  font-size: 2em;
  margin-bottom: 10px;
  color: #333;
}

.product-description {
  font-size: 1.0em;
  color: #666;
  white-space: pre-line; /* Respeta los saltos de línea */
  text-align: center;
}

/* Media Query para pantallas pequeñas (móviles) */
@media (max-width: 768px) {
  .product-card {
    grid-template-columns: 1fr;
    width: 90%; /* Ocupa todo el ancho en móviles */
  }

  .product-list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
    width: 100%;
    max-width: 1200px;
  }

  .product-image {
    width: 200px;
    height: auto;
    max-height: 200px;
    object-fit: cover;
    border-radius: 10px;
  }

}

