.App-header {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15vh;
    background-color: white;
    padding: 0 20px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background-color: #f8f8f8; /* Cambia según tu diseño */
}

.header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.logo {
    display: flex;
    align-items: center;
    font-size: 1.5em;
    margin-left: -80px; /* Mover el logo más a la izquierda */
}

.custom-logo {
    width: 270px; /* Tamaño del logo en pantallas grandes */
    height: auto; /* Mantener la proporción de aspecto */
}

.header-buttons {
    display: flex;
    gap: 10px;
}

.header-buttons .btn-instagram,
.header-buttons .btn-whatsapp {
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 15px;
}

.header-buttons .btn-instagram {
    background-color: #f7e6e6;
    color: #826363;
}

.header-buttons .btn-whatsapp {
    background-color: rgb(230, 247, 244);
    color: #826363;
}

.modal-politicas {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Fondo semi-transparente */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}
  
.modal-politicas-content {
    background-color: #ffffff;
    color: #826363;
    border: 2px solid #826363; /* Borde rojo */
    border-radius: 10px; /* Bordes redondeados */
    padding: 20px; /* Espaciado interno */
    max-width: 80%; /* Ancho máximo */
    max-height: 80%; /* Altura máxima */
    overflow: auto; /* Scroll si es necesario */
    text-align: justify; /* Alineación de texto al centro */
}

.modal-politicas-content a {
    color: #000000;
    text-decoration: none;
  }
.modal-politicas-content a:hover {
text-decoration: underline;
}

.modal-politicas-buttons {
    display: flex;
    justify-content: center; /* Centra horizontalmente los botones */
    gap: 20px; /* Espaciado entre botones */
    margin-top: 20px; /* Espacio arriba del contenedor de botones */
}

button {
    padding: 10px 20px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    background-color: #f7e6e6;
    color: #826363; 
    font-family: Roboto, sans-serif;
    font-size: 14px;
}

button:hover {
    background-color: #d3d3d3; /* Color de fondo al hacer hover */
}

button:focus {
    outline: none;
}

.menu-separator {
    color: #826363;
    width: 20px; /* Ancho del separador */
    height: 1px; /* Altura del separador (puedes ajustar esto si quieres una línea más gruesa) */
    background-color: #000; /* Color del separador (puedes cambiarlo según tu diseño) */
    margin: 0 30px; /* Espacio a ambos lados del separador */
    vertical-align: middle; /* Alineación vertical para mantener el separador centrado con el texto */
}

.menu_principal.open {
    display: flex; /* Muestra el menú si está abierto */
  }

.menu-link {
    color: #826363;
    font-family: 'Roboto', sans-serif; 
    text-decoration: none; 
    font-size: 14px;
  }
  
.menu-link:hover {
    text-decoration: underline; /* Subrayado al pasar el mouse */
}

.menu-toggle {
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    display: none; /* Oculta el botón por defecto */
  }

/* Estilos para vista móvil */
@media (max-width: 768px) {

    .header-buttons {
        flex-direction: column;
        align-items: center;
    }

    .custom-logo {
        width: 230px; /* Tamaño del logo en pantallas pequeñas */
        padding: 0 30px;
    }

    .menu_principal {
      display: none; /* Oculta el menú principal por defecto en móvil */
      flex-direction: column; /* Cambia la dirección a columna en móvil */
      position: absolute; /* Posiciona el menú de manera absoluta */
      top: 100%; /* Mueve el menú justo debajo del encabezado */
      left: 0; /* Alinea el menú a la izquierda */
      right: 0; /* Asegúrate de que ocupe todo el ancho disponible */
      background-color: #fff; /* Cambia según tu diseño */
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Sombra opcional */
      z-index: 1000; /* Asegúrate de que esté en la parte superior */
    }
 
    .menu_principal.open {
        display: flex; /* Muestra el menú si está abierto */
    }
    
    .menu-toggle {
      display: block; /* Muestra el botón de hamburguesa en móvil */
    }

}