.CarruselImg {
    height: 100vh;
    height: 70vh;
    background-color: #f7e6e6;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  
.CarruselImg_content{
    width: 80%;
    background-color: #f7e6e6;
}
  
  .slick-slide img {
    width: 90%;
    height: auto;
    border-radius: 10px;
  }
  
  .slick-prev:before, .slick-next:before {
    color: black; /* Cambia el color de las flechas de navegación */
  }
  
  .slick-dots li button:before {
    color: black; /* Cambia el color de los puntos de navegación */
  }
  