.overlay_s2 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    z-index: 1;
}

.Contactenos {
    position: relative;
    background-image: url('img/fondo_contactenos.jpg');
    background-size: cover;
    background-position: center;
    opacity: 0.9;
    height: 50vh;
    padding-bottom: 0px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.contactenos_main {
    position: relative;
    max-width: 800px;
    margin: 0 auto;
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.0);
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 2;
    font-family: roboto;
}

.titulo h2 {
    font-size: 2.5em;
    color: #ffffff;
    margin-bottom: 20px;
}

.mensaje p {
    font-size: 1.2em;
    color: #ffffff;
    margin-bottom: 20px;
}

.icono_social {
    display: flex;
    justify-content: center;
    gap: 20px;
}

.icono_social img {
    width: 40px;
    height: 40px;
    z-index: 1000;
    transition: color 0.3s;
}

.icono_social a:hover img {
    color: #007bff; /* Aplica a otras propiedades, no a imágenes */
}

/* Asegura que el contenido textual esté por encima de la capa de fondo */
.Contactenos h1, .Contactenos h3 {
    position: relative;
    z-index: 2;
    color: white;
    text-align: center;
    margin: 0;
    padding: 0;
}

.Contactenos h4 {
    font-size: 1.5em;
    color: #ffffff;
    margin-bottom: 20px;
}

.contactenos_info{
    color: white;
}