.overlay_s1 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3); /* Capa oscura con 50% de opacidad */
    z-index: 1; /* Coloca la capa por encima de la imagen de fondo */
}

.Presentacion {
    position: relative;
    background-image: url('img/fondo_s1_1.jpg');
    background-size: cover;
    background-position: top; /* Centra la imagen en ambas direcciones */
    height: 70vh; /* Ajusta según tu diseño */
    display: flex;
    justify-content: center; /* Centra horizontalmente */
    align-items: center; /* Centra verticalmente */
    text-align: center;
    opacity: 0.8; /* Transparencia del 90% */
}

.info {
    position: relative;
    z-index: 2;
    padding: 30px;
    background: rgba(0, 0, 0, 0.0); /* Fondo semitransparente opcional */
    border-radius: 10px; /* Opcional, para bordes redondeados */
}

.info_titulo, .info_sub_titulo, .info_description {
    margin: 10px 0; /* Espaciado entre los elementos de texto */
    padding: 0;
    z-index: 2;
    font-family: sans-serif;
}

.info_titulo{
    color: rgb(255, 255, 255);
    font-size: 36px;
}

.info_sub_titulo {
    color: #eed3d3;
    font-size: 24px;
}

.info_description{
    color: rgb(255, 255, 255);
}

/* Media Query para pantallas pequeñas (móviles) */
@media (max-width: 768px) {

    .Presentacion {
        position: relative;
        background-image: url('img/fondo_s1.jpg');
        background-size: cover;
        background-position: top; /* Centra la imagen en ambas direcciones */
        height: 70vh; /* Ajusta según tu diseño */
        display: flex;
        justify-content: center; /* Centra horizontalmente */
        align-items: center; /* Centra verticalmente */
        text-align: center;
        opacity: 0.8; /* Transparencia del 90% */
    }
}    