.PageProductosBody {
  display: flex;
  justify-content: center; /* Alinea horizontalmente */
  align-items: center;     /* Alinea verticalmente */
  height: 60vh;           /* Ocupa la altura completa de la ventana */
  padding: 20px;           /* Opcional: agrega espacio interno alrededor */
  color: #826363;
  background-color: #fff9f9;
  background-size: cover; /* Hace que la imagen cubra todo el fondo */
  background-position: center; /* Centra la imagen */
  background-repeat: no-repeat; /* Evita que la imagen se repita */
}

.PageProductosContent{
  align-items: center;
  display: inline;
}

.PageProductosMensaje {
  max-width: 600px;        /* Ancho máximo para limitar el tamaño del contenido */
  text-align: left;      /* Centra el texto */
  color: #826363; 
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.PageProductosMensaje p {
  text-align: center; /* Centra el texto del párrafo */
  margin-bottom: 20px; /* Añade espacio debajo del párrafo */
}

.PageProductosMensajeTitulo {
  color: #826363;
  font-family: Roboto, sans-serif;
  font-size: 42px;
  text-align: center; /* Alinea el título en el centro */
  margin-bottom: 20px; /* Espaciado opcional debajo del título */
}

