.Footer_main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 1200px; /* Ajusta el ancho máximo según sea necesario */
}

.pink-separator {
    color: #f0f0f0; /* Color rosado */
    font-weight: bold; /* Opcional: para hacer las barras más visibles */
}

.Footer p {
    margin: 0;
    color: aliceblue;
    font-family: Roboto, sans-serif;
    font-size: 12px; /* Ajusta el tamaño del texto según sea necesario */
}

.legal-link {
    color: #f3bed8;
    font-family: 'Roboto', sans-serif; /* Fuente Roboto */
    text-decoration: none; /* Sin subrayado */
    font-size: 12px;
  }
  
.legal-link:hover {
    text-decoration: underline; /* Subrayado al pasar el mouse */
}